interface PaymentControlsSharedModel {
  status: string[];
  query: string;
}

export interface PaymentsControlsModel extends PaymentControlsSharedModel {
  amount: {
    from?: number;
    to?: number;
  };
  executionDate: {
    from?: string;
    to?: string;
  };
  createdByMe?: boolean;
}

export interface FlatPaymentsControlsPayloadModel
  extends PaymentControlsSharedModel {
  amountFrom: number | string; // String value should be empty so that api doesnt crash
  amountTo: number | string;
  executionDateFrom: string;
  executionDateTo: string;
  from: number;
  mergeItems: boolean;
}
